import React from "react";
import { Redirect, Route, Switch } from "react-router";
import { Router } from "react-router-dom";

import Final from "./Final/Final";
import First from "./First/First";
import Second from "./Second/Second";
import Third from "./Third/Third";
import Fourth from "./Fourth/Fourth";
import Fifth from "./Fifth/Fifth";
import "./App.css";
import { createBrowserHistory } from "history";

function App() {
  const history = createBrowserHistory();
  return (
    <Router history={history}>
      <Switch>
        <Route exact path="/rhyme" component={First} />
        <Route exact path="/rhyme/first" component={First} />
        <Route exact path="/rhyme/second" component={Second} />
        <Route exact path="/rhyme/third" component={Third} />
        <Route exact path="/rhyme/fourth" component={Fourth} />
        <Route exact path="/rhyme/fifth" component={Fifth} />
        <Route exact path="/rhyme/final" component={Final} />
      </Switch>
    </Router>
  );
}

export default App;
